.book {
    margin-left: 15%;
    margin-right: 15%;
}

.name {
    padding-bottom: 0% !important;
}

.divider {
    margin-top: 15px;
    margin-bottom: 40px;
}

.block {
    display: flex;
    flex-direction: row;
    padding-bottom: 3%;
}

.img {
    max-width: 312px;
    max-height: 460px;
}

.group {
    display: flex;
    flex-direction: column; 
    padding-left: 4%;
}

.description {
    display: flex;
    min-width: 950px;
}

.label {
    font-size: 18px;
    min-width: max-content;
}

.list {
    min-width: 0%;
    margin: 24px !important;
}

.data {
    font-size: 18px;
    display: flex;
    justify-content: flex-end;
    min-width: max-content;
}

.price {
    font-size: 36px;
}

.ruble {
    font-size: 24px;
}

.title {
    padding-top: 3%;
}

.text {
    padding-left: 6%;
    padding-right: 6%;
}

.row {
   display: flex;
   justify-content: center;
}

.document {
    display: flex;
    flex-direction: row;
    align-items: center;
    min-height: 90vh;
}

.page {
    min-width: 65vh;
    display: flex;
    justify-content: center;
}

.button {
    width: 220px;
    height: 51.74px;
    font-size: 18px;
    margin-top: 5%;
}

.recommendations {
    display: flex;
    align-items: center;
}

.left {
    display: flex;
    justify-content: flex-end;
}

.spin {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
}